=====================================================================*/
.rtl {
    text-align: right;
    direction: rtl;
  }
  
  /*----------------------------------------------------*/
  
  @import "variables";
  /*---------------------------------------------------- */
  
  @import "mixins";
  /*---------------------------------------------------- */
  
  @import "admin/predefine";
  /*---------------------------------------------------- */
  html[dir="rtl"] {
    .primary-btn {
      &.white {
        &:hover {
          @include gradient(90deg, $primary-color2 0%, $primary-color3 100%);
          box-shadow: 0px 10px 20px rgba(108, 39, 255, 0.3);
        }
      }
      span {
        &.pr {
          padding-right: 0;
        }
        &.pl {
          padding-right: 8px;
          float: right;
          position: relative;
          top: 8px;
          left: 8px;
        }
      }
      &.small {
        .ti-plus,
        .ti-search {
          float: right;
          position: relative;
          top: 9px;
        }
      }
      &.fix-gr-bg {
        .ti-check {
          float: right;
          position: relative;
          top: 13px;
          left: 7px;
        }
        .ti-pencil-alt {
          float: right;
          position: relative;
          top: 8px;
          left: 7px;
        }
      }
      &.small {
        .ti-check {
          float: right;
          position: relative;
          top: 8px;
          left: 7px;
        }
      }
    }
  
    .primary-input {
      ~ .focus-border {
        left: auto;
        right: 0;
      }
    }
  
    .common-radio {
      &:checked {
        ~ label {
          &:after {
            left: auto;
            right: -3px;
          }
        }
      }
      &:empty {
        ~ label:before {
          left: auto;
          right: 0;
        }
      }
    }
  
    .common-checkbox + label {
      &:before {
        margin-left: 14px;
        margin-right: 0;
      }
    }
  
    .input-right-icon {
      button {
        i {
          left: 22px;
          display: none;
        }
      }
      button {
        &.primary-btn-small-input {
          position: relative;
          left: 100px;
        }
      }
    }
  
    #main-content {
      margin-right: 15%;
      margin-left: 0;
      @media (max-width: 1370px) {
        margin-right: 20%;
      }
      @media (max-width: 991px) {
        margin-right: 0;
      }
    }
    .main-title {
      h3 {
        text-align: right;
      }
    }
  }
  
  @import "admin/reset";
  /*---------------------------------------------------- */
  html[dir="rtl"] {
    .ml-10 {
      margin-left: 0;
      margin-right: 10px;
    }
    .ml-20 {
      margin-left: 0;
      margin-right: 20px;
    }
    .ml-40 {
      margin-left: 0;
      margin-right: 40px;
    }
    .mr-10 {
      margin-left: 10px;
      margin-right: 0;
      @media (max-width: 575px) {
        margin-left: 0;
      }
    }
    .mr-30 {
      margin-left: 30px;
      margin-right: 0;
    }
    .to-do-list.up_buttom {
      justify-content: space-between;
      display: flex;
    }
  }
  
  @import "admin/responsive";
  /*---------------------------------------------------- */
  
  @import "admin/component";
  /*---------------------------------------------------- */
  html[dir="rtl"] {
    .school-table .dropdown .dropdown-toggle:focus,
    .school-table .dropdown .dropdown-toggle:hover {
      @include gradient(90deg, $primary-color2 0%, $primary-color3 100%);
      box-shadow: 0px 10px 20px rgba(108, 39, 255, 0.3);
    }
    
    .school-table {
      .dropdown {
        .dropdown-toggle {
          // padding: 5px 30px 5px 7px;
          padding: 5px 20px 5px 0px;
          font-size: 11px;
          margin-right: 0px;
          &:after {
            left: 105%;
          }
          &:focus {
            &:after {
              top: 1px;
            }
          }
        }
        .dropdown-menu {
          box-shadow: 0px 10px 20px rgba(108, 39, 255, 0.3);
        }
      }
    }
  }
  
  @import "admin/login";
  /*---------------------------------------------------- */
  
  @import "admin/sidebar";
  /*---------------------------------------------------- */
  html[dir="rtl"] {
    .staff-details li.edit-button, .student-details li.edit-button {
      text-align: left;
    }
    .student-meta-box .student-meta-top {
      background-color: $sidebar_bg;
    }
    .client_setting_area .client_area_content {
      left: 0;
      right: auto;
    }
    #sidebar {
      right: 0;
      &.active {
        z-index: 99999;
        @media (max-width: 991px) {
          margin-right: 0;
        }
      }
      @media (max-width: 991px) {
        margin-right: -80%;
      }
      .sidebar-header {
        text-align: right;
      }
      ul {
        li {
          a {
            text-align: right;
            display: flex;
            border-right: 6px solid transparent;
            border-left: 0px solid transparent;
            &:hover, &.active {
              border-left: 0;
              border-right: 6px solid $primary-color2;
            }
          }
          ul {
            li {
              a {
                margin-left: 0;
                padding-right: 55px;
                padding-left: 0px;
              }
            }
          }
        }
      }
      .dropdown-toggle {
        &:after {
          left: 20px;
          right: auto;
        }
      }
    }
  }
  
  @import "admin/header";
  /*---------------------------------------------------- */
  html[dir="rtl"] {
    .search-bar {
      .ti-search {
        left: 0;
      }
    }
    input {
      &:focus {
        @include placeholder {
          left: -16px;
        }
      }
    }
    .nav.navbar-nav {
      &.mr-auto {
        &.nav-buttons {
          &.flex-sm-row {
            margin: 0 auto !important;
          }
        }
      }
    }
    .up_dash_menu{
        .nav.navbar-nav {
            &.mr-auto {
                &.nav-buttons {
                    &.flex-sm-row {
                        margin: inherit !important;
                    }
                }
            }
        }
    }
    
    .admin {
      .navbar {
        .right-navbar {
          .dropdown {
            .dropdown-menu {
              left: 0;
              right: auto;
            }
            .badge {
              left: -10px;
              @include gradient(90deg, $primary-color2 0%, $primary-color3 100%);
            }
            .primary-btn {
              @include gradient(90deg, $primary-color2 0%, $primary-color3 100%);
            }
          }
        }
      }
    }
    .sms-breadcrumb {
      .bc-pages {
        a {
          margin-left: 28px;
          margin-right: 0;
          &:after {
            right: auto;
            left: -16px;
          }
          &:last-child {
            margin-left: 0;
          }
        }
      }
    }
  }
  @import "admin/student";
  /*---------------------------------------------------- */
  
  @import "admin/front-cms";
  /*---------------------------------------------------- */
  
  @import "admin/exam";
  /*---------------------------------------------------- */
  
  @import "admin/fees";
  /*---------------------------------------------------- */
  
  @import "admin/settings";
  /*---------------------------------------------------- */
  
  @import "admin/home";
  /*---------------------------------------------------- */
  
  @import "admin/footer";
  /*---------------------------------------------------- */
  
  @import "client/reset";
  /*---------------------------------------------------- */
  
  @import "client/predefine";
  /*---------------------------------------------------- */
  
  @import "client/header";
  /*---------------------------------------------------- */
  html[dir="rtl"] {
    .client {
      .header-area {
        .navbar {
          .nav {
            .nav-item {
              margin-right: 0;
              margin-left: 45px;
            }
          }
        }
      }
    }
  }
  @import "client/home";
  /*---------------------------------------------------- */
  html[dir="rtl"] {
    .client {
      .news-item {
        .news-text {
          right: 10%;
          left: auto;
          h4 {
            padding-right: 0;
            padding-left: 20px;
          }
        }
      }
      .events-item {
        .card {
          .card-body {
            .date {
              right: 20px;
              left: auto;
            }
          }
        }
      }
      .testimonial-area {
        .owl-nav {
          .owl-prev {
            margin-right: 0;
            margin-left: 30px;
          }
        }
      }
      .footer_area {
        .f_widget {
          ul {
            padding-right: 0;
          }
        }
      }
      .single-footer-widget {
        .social_widget {
          text-align: left;
        }
        .social_widget {
          a {
            margin-left: 0;
            margin-right: 10px;
          }
        }
      }
    }
  }
  
  @import "client/footer";
  /*---------------------------------------------------- */
  html[dir="rtl"] {
    .up_dash_menu {
      display: flex;
    }
    div.dt-buttons {
      float: left;
    }
    button.dt-button,
    div.dt-button,
    a.dt-button {
      border-left: 0;
      border-right: 1px solid $primary-color;
    }
    table {
      &.dataTable {
        thead {
          .sorting:after {
            right: 4px;
            left: auto;
          }
          .sorting_asc:after {
            left: auto;
            right: 0px;
          }
          .sorting_desc:after {
            left: auto;
            right: 2px;
          }
        }
      }
    }
  
    .dataTables_wrapper {
      .dataTables_filter {
        input {
          margin-right: 0.5em;
          margin-left: 0;
          &:focus {
            @include placeholder {
              left: -16px;
            }
          }
        }
        > label {
          &:before {
            left: auto;
            right: 0;
          }
        }
      }
      .dataTables_paginate {
        .paginate_button {
          i {
            @include transform(rotate(180deg));
            display: inline-block;
          }
        }
      }
      .dataTables_info {
        float: right;
      }
    }
  
    .common-calendar .fc-state-default.fc-corner-left,
    .common-calendar .fc-button.fc-state-default {
      &:hover {
        @include gradient(90deg, $primary-color2 0%, $primary-color3 100%);
        box-shadow: 0px 10px 20px rgba(108, 39, 255, 0.3);
      }
    }
  
    .nice-select {
      &:after {
        right: 10px;
      }
      &.bb {
        &:before {
          left: auto;
          right: 0;
        }
      }
      .option {
        padding-right: 18px;
        padding-left: 29px;
        text-align: right;
      }
    }
    table.dataTable, table.dataTable th, table.dataTable td {
      box-sizing: border-box;
    }
  }
  
  @import "variables";
  /*---------------------------------------------------- */
  
  @import "update";
  /*---------------------------------------------------- */

  //data table
  html[dir="rtl"] {
    @media (max-width: 1200px){
        .dataTables_filter > label {
            display: none;
        }
    }
  }